.contact-page {
    max-width: 100%; /* Ensure it can stretch to 100% of the container */
    width: 100%;
    margin: 2rem auto;
    padding: 1rem;
    font-family: Arial, sans-serif;
    background-color: #f0f7fc;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow-y: auto; /* Allows scrolling if the content exceeds the height */
    max-height: 90vh; /* Prevent the form from being too long */
}

.contact-page h1 {
    text-align: center;
    margin-bottom: 1.5rem;
    color: #333;
}

.contact-form {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem; /* Adds space between form groups */
}

.contact-form .form-group {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
}

.contact-form label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
    color: #555;
}

.contact-form input,
