    width: 100%; /* Make inputs take up the full width */
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
}

.contact-form input:focus,
.contact-form textarea:focus {
    outline: none;
    border-color: #ec7063;
    box-shadow: 0 0 4px rgba(0, 123, 255, 0.5);
}

.contact-form button {
    background-color: #ec7063;
