header {
    background-color: #85c1e9;
    padding: 0.50rem 2rem; /* Reduced padding for less depth but keep left and right padding */
    width: 100%; /* Ensure it spans the full width */
    font-size: 18px;
    font-weight: 600; /* Semi-bold for header */
    display: flex; /* Flexbox for easier layout control */
    justify-content: space-between; /* Spread out the navigation and social links */
    align-items: center; /* Vertically center the content */
    position: fixed; /* Keeps header at the top of the page */
    top: 0; /* Place it at the very top */
    left: 0; /* Ensure it starts from the very left */
    right: 0; /* Ensure it ends at the very right */
    z-index: 1000; /* Ensure it stays on top of other content */
    margin: 0; /* Remove any margin */
    height: 3rem;
}

/* Ensure the main body content is not hidden under the fixed header */
body {
    margin-top: 5rem; /* Adjust this depending on the height of your header */
    padding: 0;
    height: 100vh; /* Full viewport height */
    overflow: hidden; /* Prevent scrollbars */
}

.nav-list {
    list-style: none;
    padding: 0.5rem;
    display: flex;
    align-items: center;
    font-size: 15px; /* Slightly smaller font size */
    font-weight: 400; /* Regular weight for nav links */
    grid-gap: 1.5rem;
    gap: 1.5rem; /* Reduce spacing between links */
    white-space: nowrap; /* Prevent links from wrapping to the next line */
}

.dropdown {
    position: relative;
}

.dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #f0f7fc;
    border: 1px solid #ddd;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    box-shadow: none;
    border-radius: 0;
    padding: 0.5rem 1rem;
    list-style: none;
    z-index: 10;
    display: none;
    width: auto;
}

/* Targets list elements inside the dropdown list. li represents a list item */
.dropdown-menu li {
    padding: 0.5rem 0.5rem;
    width: 100%;
}
/* Targets the <a> tag (hyperlink) inside each list item of the dropdown menu */
.dropdown-menu li a {
    text-decoration: none;
    color: #000000;
    display: block;
    transition: background-color 0.2s ease-in-out;
    width: auto;
}

.dropdown:hover .dropdown-menu,
.dropdown:focus-within .dropdown-menu {
    display: block;
}

.social-links {
    font-size: 12px; /* Smaller font size for social links */
    display: flex;
    grid-gap: 1rem;
    gap: 1rem; /* Reduce gap between icons */
    align-items: center; /* Vertically align the icons */
}

.social-links li a {
    text-decoration: none; /* Remove underlining */
    display: inline-flex; /* Ensure the icons are inline */
    justify-content: center;
    align-items: center;
    transition: color 0.2s ease, transform 0.2s ease; /* Smooth transition */
    font-size: 12px;
}

.social-links li a:hover {
    transform: scale(1.1); /* Optional: icon grows slightly on hover */
}
