@import url('https://fonts.googleapis.com/css?family=Inter&display=swap');

h1, h2, h3 {
  font-family: 'Inter', sans-serif;
  font-weight: 400; /* Semi-bold for headings */
  color: black;
  text-align: center;
  font-size: 40px;
}
html, body {
  height: 100%;  /* Ensure the elements take the full viewport height */
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  background-image: url('../src/components/images/pexels-padrinan-255379.jpg'); /* Path to your image */
  background-size: cover; /* Cover the entire viewport */
  background-repeat: no-repeat; /* Avoid tiling the background */
  background-attachment: fixed; /* Ensure background stays fixed during scroll */
  background-position: center; /* Center the image */
  overflow-x: hidden; /* Prevent horizontal scrolling */
  overflow-y: auto;
}
/* Additional styles for other elements */
body {
  line-height: 1.6;
  font-size: 18px;
  color: black;
}

p {
  margin-right: 2rem;
  margin-left: 2rem;
  line-height: 1.6; /* Improve readability with increased line spacing */
  font-size: 18px; /* Default size for body text */
  font-weight: 300;
  color: black; /* Optional: Adjust paragraph text color */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 40px;
}

h2 {
  font-size: 25px;
}

/* Global Button Style */
button {
  padding: 0.75rem 1.5rem; /* Consistent padding for all buttons */
  font-family: 'Inter', sans-serif; /* Use the same font as the website */
  font-size: 1rem; /* Default font size */
  font-weight: 400; /* Regular font weight */
  border: none; /* Remove default button border */
  border-radius: 4px; /* Rounded corners */
  cursor: pointer; /* Pointer cursor on hover */
  background-color: #ec7063;
  color: black; /* White text color */
  text-align: center; /* Center align text */
  transition: background-color 0.3s ease, transform 0.2s ease; /* Smooth transition effects */
}

/* Button Hover Effect */
button:hover {
  background-color: #b1544a; /* Darker when hovered */
  transform: translateY(-2px); /* Slight lift effect on hover */
}

/* Button Active Effect (When Clicked) */
button:active {
  background-color: #763832; /* Even darker when clicked */
  transform: translateY(1px); /* Slight push down effect when clicked */
}

/* Disabled Button */
button:disabled {
  background-color: #cccccc; /* Light gray for disabled state */
  color: #666666; /* Darker gray text */
  cursor: not-allowed; /* Indicating it's not clickable */
  transform: none; /* No transformation effect */
}

/* Global Button for Links (in case you have links styled as buttons) */
a.button-link {
  display: inline-block; /* Treat links as buttons */
  padding: 0.75rem 1.5rem;
  font-family: 'Inter', sans-serif;
  font-size: 1rem;
  border-radius: 4px;
  text-align: center;
  text-decoration: none; /* Remove underline from links */
  background-color: #85c1e9 ; /* Primary color */
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

/* Hover and Active Effects for Button Links */
a.button-link:hover {
  background-color: #6491af;
  transform: translateY(-2px);
}

a.button-link:active {
  background-color: #324857;
  transform: translateY(1px);
}

a.button-link:disabled {
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
  transform: none;
}

@media (max-width: 768px) {
  html, body {
    background-attachment: scroll; /* Use scrollable background on smaller screens */
  }
  .section {
    grid-template-columns: 1fr; /* Stack sections on smaller screens */
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0.5rem;
  }
}
